
import * as _ from 'lodash';


export function searchList(searchWord: String, tempList: any[]): any[] {
    if(searchWord == null || searchWord == '') {
        return _.clone(tempList);;
      }
  
      
      let tl: any[] = _.clone(tempList);
      tl[0] = _.clone(tempList[0]);
      
      let newTagMap = new Map();
  
      tl[0].tagMap.forEach((value: any[], key: string) => {
        // if(value.root_title)
        // indexOf
  
        let searcharr: any[] = [];
        for(var i = 0 ; i < value.length; ++i) {
          let post = value[i];
          if(post.root_title.indexOf(searchWord) >= 0) {
            searcharr.push(post);
          }
        }
        newTagMap.set(key, searcharr);
      });
  
      tl[0].tagMap = newTagMap;

      return tl;
}


